    width: 40px;
    height: 40px;
    color: #000000;
    text-decoration: none; }
    @media (min-width: 1600px) {
      .main-nav .nav__item a {
        width: 50px;
        height: 50px; } }
    .main-nav .nav__item a:after {
      content: '';
