.splash {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #FFFFFF;
  color: #000000;
  text-align: center;
  visibility: visible;
  z-index: 30;
  transition: opacity 0.3s ease calc(0.7s / 2), visibility 0s ease calc(0.7s * 2); }
  .splash__inner {
    position: absolute;
    top: 50%;
    left: 50%;
    max-width: 1000px;
    transform: translateX(-50%) translateY(-50%); }
  .splash__header {
    opacity: 0;
    transition: all calc(0.7s / 2) ease; }
  .splash__subtext {
    font-size: 18px;
    opacity: 0;
    transition: all calc(0.7s / 2) ease calc(0.7s / 2); }
    @media (min-width: 800px) {
